<template>
  <div class="signup-container">
    <b-form @reset="onReset" v-if="show" class="signup-container__form justify-content-center">
      <b-form-group id="input-group-1" label="Логин:" label-for="input-1">
        <b-form-input
            id="input-1"
            v-model="form.login"
            type="text"
            placeholder="Введите логин"
            minlength="3"
            maxlenght="32"
            autofocus
            required
        ></b-form-input>
      </b-form-group>

      <b-form-group id="input-group-2" label="Почтовый адрес:" label-for="input-2">
        <b-form-input
            id="input-2"
            v-model="form.email"
            type="email"
            placeholder="Введите почту"
            maxlenght="32"
            required
        ></b-form-input>
      </b-form-group>

      <b-form-group id="input-group-3" label="Ваш пароль:" label-for="input-3">
        <b-form-input
            id="input-3"
            v-model="form.password"
            type="password"
            placeholder="Введите пароль"
            minlength="4"
            maxlenght="32"
            required
        ></b-form-input>
      </b-form-group>

      <b-form-group id="input-group-4" label="Повторите пароль:" label-for="input-4">
        <b-form-input
            id="input-4"
            v-model="form.repeatPassword"
            type="password"
            placeholder="Введите пароль"
            minlength="4"
            maxlenght="32"
            required
        ></b-form-input>
      </b-form-group>

      <b-form-group id="input-group-5" label="Выберите вашу роль:" label-for="radiobutton" v-slot="{ ariaDescribedby }">
        <b-form-radio-group
            id="radiobutton"
            v-model="form.user_type"
            type="radio"
            :aria-describedby="ariaDescribedby"
            required
        >
          <b-form-radio value="1">Специалист службы ЭРТОС</b-form-radio>
          <b-form-radio value="2">Застройщик приаэродромной зоны</b-form-radio>
        </b-form-radio-group>
      </b-form-group>

      <div class="button__group">
        <b-button type="submit" variant="primary" class="signup-container__submit" @click.prevent="onSubmit">
          Зарегистрироваться
        </b-button>
      </div>
    </b-form>
    <div>
      <b-modal id="modal-login" content-class="shadow" hide-header hide-footer centered>
        <p class="my-2">
          Пароли должны совпадать!
        </p>
      </b-modal>
    </div>
  </div>
</template>

<script>
import {HTTP} from '@/main';

export default {
  name: 'SignUp',
  data() {
    return {
      form: {
        email: '',
        login: '',
        password: '',
        user_type: '',
        repeatPassword: '',
      },
      show: true,
    };
  },
  methods: {
    async onSubmit() {
      if (this.form.password !== this.form.repeatPassword) {
        this.$bvModal.show('modal-login');
        setTimeout(function () {
          this.$bvModal.hide('modal-login');
        }.bind(this), 2000);
      }
      try {
        let response = await HTTP.post('/registration/', this.form)
        if (response.status === 201) {
          this.onReset();
        }
      } catch (e) {
        this.errors.push(e)
        console.log(this.errors)
      }
    },


    /*let response = */
    // if (response.status === 200) {
    //   alert('Вы успешно зарегистрированы!')
    //   this.onReset();
    // }
    /*      if (this.form.login && this.form.email &&
              this.form.password && this.form.repeatPassword &&
              this.form.user_type && this.form.password === this.form.repeatPassword) {
            new Sender.methods.sendData('/registration/', this.form);
          } else if (this.form.login && this.form.email &&
              this.form.password && this.form.repeatPassword &&
              this.form.user_type && this.form.password !== this.form.repeatPassword){
            alert('Пароли должны совпадать');
          }*/
    onReset(/*event*/) {
      // event.preventDefault();
      this.form.email = '';
      this.form.login = '';
      this.form.password = '';
      this.form.repeatPassword = '';
      this.form.user_type = '';

      this.show = false;
      this.$nextTick(() => {
        this.show = true;
      });
      this.$emit('sendResetRegistrationOnPage', this.show)
    },
  },
};
</script>
<style scoped>
.signup-container {
  margin: 2rem 3rem;
}

#input-1, #input-2, #input-3, #input-4 {
  border-radius: .9rem;
  border-color: white;
  background-image: linear-gradient(120deg, rgba(161, 196, 253, .4) 0%, rgba(194, 233, 251, .8) 100%);
  opacity: .7;
}

.button__group {
  display: flex;
  justify-content: space-around;
}

.signup-container__submit {
  margin-top: 2rem;
  text-align: center;
  background-image: linear-gradient(120deg, #9effa8bf 0%, #9effa8bf 100%);
  border: none;
  border-radius: .9rem;
  font-weight: 500;
  color: #2f2f2f;
}

.signup-container__submit:hover {
  background-image: linear-gradient(120deg, #cdf1d5 0%, #cdecd4 100%);
  color: #151a40;
}

@media (min-width: 1000px) and (max-width: 1390px) and (max-height: 900px) {
  .signup-container {
    margin: 1.5rem 2rem;
  }
}

@media (min-width: 600px) and (max-width: 780px) and (max-height: 1280px) {
  .signup-container {
    margin: 1rem 1.5rem;
  }
}
</style>
