<template>
  <div class="object-panel-container" v-if="closeForm === false">
    <div class="object-panel-container__title-container">
      <h6 class="object-panel-container__title" v-if="role === '1'">Специалист службы ЭРТОС</h6>
      <h6 class="object-panel-container__title" v-if="role === '2'">Застройщик приаэродромной зоны</h6>
      <button class="object-panel-container__close-button" @click="closeForm = true">
        <b-icon icon="x-circle-fill" font-scale="1.8" variant="warning"></b-icon>
      </button>
    </div>
    <b-form class="object-panel-container__form-container"
            @submit.prevent>
      <div class="object-panel-container__select-container">
        <b-form-select v-model="selected" :options="options" required v-if="role !== '2'"></b-form-select>
        <b-form-select v-model="selected" :options="optionsDev" required v-else></b-form-select>
        <b-button type="button" variant="primary" class="location-button__arp w-25"
                  v-b-popover.hover.top="'Указать местоположение объекта'" v-if="selected !== null && selected !== 2"
                  @click="sendClickOnCoordinateButton">
          <b-icon icon="geo-alt" font-scale="1.5" variant="info" animation="cylon-vertical"
                  v-if="clickCounter !== 1"></b-icon>
          <b-icon icon="geo-alt" font-scale="1.5" variant="info" v-else></b-icon>
        </b-button>
        <b-button type="button" variant="primary" class="location-button__arp w-25"
                  v-b-popover.hover.top="'Выбор направления объекта'" v-if="selected === 2"
                  @click="sendDirectionOnMap">
          <b-icon icon="arrow-up-right" font-scale="1.5" variant="info" animation="cylon"
                  v-if="clickCoordinate === false"></b-icon>
          <b-icon icon="arrow-up-right" font-scale="1.5" variant="info" v-else></b-icon>
        </b-button>
      </div>
      <div v-if="selected === 1"
           id="arpGroup"
           class="form-container">
        <b-form-group label="Радиус цилиндра r (м):" label-for="radiusCylinder">
          <b-form-input
              id="radiusCylinder"
              v-model.number="ArpFormData.r"
              type="number"
              placeholder="Введите радиус цилиндра"
              min="1"
          ></b-form-input>
        </b-form-group>

        <b-form-group label="Угол α (°):" label-for="angleAlpha">
          <b-form-input
              id="angleAlpha"
              v-model.number="ArpFormData.a"
              type="number"
              placeholder="Введите угол альфа"
              min="1"
          ></b-form-input>
        </b-form-group>

        <b-form-group label="Радиус конуса R (м):" label-for="radiusCone">
          <b-form-input
              id="radiusCone"
              v-model.number="ArpFormData.R"
              type="number"
              placeholder="Введите радиус конуса"
              min="1"
          ></b-form-input>
        </b-form-group>

        <b-form-group label="Радиус цилиндра j (м):"
                      label-for="radiusCyl2">
          <b-form-input
              v-b-popover.hover.top="'Только ветряная турбина(ы) [крупная металлическая конструкция]'"
              id="radiusCylinder2"
              v-model.number="ArpFormData.j"
              type="number"
              placeholder="Введите радиус цилиндра"
              min="1"
          ></b-form-input>
        </b-form-group>

        <b-form-group label="Высота цилиндра j (м):"
                      label-for="heightCyl2">
          <b-form-input
              class="important-input"
              v-b-popover.hover.top="'Только ветряная турбина(ы) [крупная металлическая конструкция]:'"
              id="heightCylinder2"
              v-model.number="ArpFormData.h"
              type="number"
              placeholder="Введите высоту цилиндра"
              min="1"
              required
          ></b-form-input>
        </b-form-group>
      </div>

      <div v-if="selected === 2" id="grmGroup" class="form-container">
        <div class="form-container__items">
          <b-form-group label="a (м):" label-for="grm_a">
            <b-form-input
                id="grm_a"
                v-model.number="GrmFormData.a"
                type="number"
                placeholder="Введите a"
                min="1"
                @change="updateRadius"
            ></b-form-input>
          </b-form-group>

          <b-form-group label="b (м):" label-for="grm_b">
            <b-form-input
                id="grm_b"
                v-model.number="GrmFormData.b"
                type="number"
                placeholder="Введите b"
                min="1"
            ></b-form-input>
          </b-form-group>
        </div>

        <div class="form-container__items">
          <b-form-group label="h (м):" label-for="grm_h">
            <b-form-input
                id="grm_h"
                v-model.number="GrmFormData.h"
                type="number"
                placeholder="Введите h"
                min="1"
            ></b-form-input>
          </b-form-group>

          <b-form-group label="r (м)" label-for="grm_r">
            <b-form-input
                v-b-popover.hover.top="'r + a'"
                id="grm_r"
                v-model.number="GrmFormData.r"
                type="number"
                placeholder="Введите радиус цилиндра"
                min="1"
                disabled
            ></b-form-input>
          </b-form-group>
        </div>

        <div class="form-container__items">
          <b-form-group label="D (м)" label-for="grm_D">
            <b-form-input
                id="grm_D"
                v-model.number="GrmFormData.D"
                type="number"
                placeholder="Введите высоту цилиндра"
                min="1"
            ></b-form-input>
          </b-form-group>

          <b-form-group label="H (м)" label-for="grm_H">
            <b-form-input
                id="grm_H"
                v-model.number="GrmFormData.H"
                type="number"
                placeholder="Введите высоту цилиндра"
                min="1"
            ></b-form-input>
          </b-form-group>
        </div>

        <div class="form-container__items">
          <b-form-group label="L (м)" label-for="grm_L">
            <b-form-input
                id="grm_L"
                v-model.number="GrmFormData.L"
                type="number"
                placeholder="Введите высоту цилиндра"
                min="1"
            ></b-form-input>
          </b-form-group>

          <b-form-group label="φ (°)" label-for="grm_Fi">
            <b-form-input
                v-b-popover.hover.top="'Введите высоту цилиндра'"
                id="grm_Fi"
                v-model.number="GrmFormData.fi"
                type="number"
                placeholder="Введите высоту цилиндра"
                min="15"
                required
            ></b-form-input>
          </b-form-group>
        </div>
        <!--
                <b-button type="button" variant="primary" class="add-button"
                          @click="sendDirectionOnMap()">
                  Выбор направления объекта
                </b-button>-->
      </div>

      <div v-if="selected === 3" id="devGroup" class="form-container">
        <b-form-group label="Высота объекта (относительная, в м):" label-for="objectHeight">
          <b-form-input
              id="objectHeight"
              v-model.number="DevDotFormData.h"
              type="number"
              placeholder="Введите высоту объекта"
              min="1"
              required
          ></b-form-input>
        </b-form-group>

        <b-button type="submit" variant="primary" class="add-button" @click="sendDataToMap()">
          Рассчитать
        </b-button>
      </div>

      <div v-if="selected === 4" id="devRouteGroup" class="form-container">
        <b-form-group label="Высота объекта (относительная, в м):" label-for="objectHeight">
          <b-form-input
              id="objectHeight"
              v-model.number="DevRouteFormData.h"
              type="number"
              placeholder="Введите высоту объекта"
              min="1"
              required
          ></b-form-input>
          <div v-if="clickCoordinate === true">
            <div class="coordinate-panel__inputs" v-for="(info, index) in DevRouteFormData.polyline" :key="info">
              <b-form-input
                  class="w-50"
                  id="coordinate1"
                  v-model.number="info[0]"
                  type="number"
                  placeholder="Введите lat"
                  min="1"
                  step="0.000001"
              ></b-form-input>
              <b-form-input
                  class="w-50"
                  id="coordinate2"
                  v-model.number="info[1]"
                  type="number"
                  placeholder="Введите lon"
                  min="1"
                  step="0.000001"
              ></b-form-input>
              <button type="button" class="form-container__delete-button" @click="deleteDevElem(index)">
                <b-icon icon="x-circle-fill" font-scale="1.8" variant="warning"></b-icon>
              </button>
            </div>
          </div>
        </b-form-group>
      </div>

      <b-button type="submit" variant="primary" class="add-button" v-if="selected === 1 || selected === 2"
                @click="sendDataToMap()">
        Добавить
      </b-button>

      <b-button type="submit" variant="primary" class="add-button"
                v-if="selected === 4 && clickCoordinate === true"
                @click="sendDataToMap()"
      >
        Сохранить объект
      </b-button>
    </b-form>

    <div>
      <b-modal id="modal-info" hide-backdrop content-class="shadow" hide-header hide-footer centered>
        <p class="my-2">
          {{ modals }}
        </p>
      </b-modal>
    </div>

    <div>
      <b-modal id="modalDevRoute" hide-backdrop content-class="shadow" hide-header hide-footer centered>
        <p class="my-4">Для установки объекта укажите начальную координату</p>
        <b-button class="mt-3" variant="outline-success" block @click="$bvModal.hide('modalDevRoute')">Всё понятно
        </b-button>
      </b-modal>
    </div>

  </div>
</template>

<script>
import {bus} from '@/main'

export default {
  name: 'MapObjectsPanel',
  props: {
    devRoute_data: {
      type: Array,
      default() {
        return []
      }
    }
  },
  components: {
    // eslint-disable-next-line vue/no-unused-components
    bus
  },
  data() {
    return {
      /* Выбор типа объекта */
      selected: null,
      options: [
        {value: null, text: 'Выберите тип навигационного средства:'},
        {value: 1, text: 'АРП'},
        {value: 2, text: 'КРМ (двухканальный)'},
      ],
      optionsDev: [
        {value: null, text: 'Выберите тип объекта:'},
        {value: 3, text: 'Труба'},
        {value: 4, text: 'Трубопроводная магистраль'},
      ],

      /* Отображение информации об объектах в формах */
      ArpFormData: {
        id: null,
        userId: null,
        objId: 1,
        r: 500,
        a: 1.0,
        R: 3000,
        j: 10000,
        h: 52,
        latitude: null,
        longtitude: null,
        color: '#54b0a2',
        fillColor: '#83e3d4',
      },
      GrmFormData: {
        id: null,
        objId: 2,
        userId: null,
        a: 40,
        b: 500,
        h: 70,
        r: 6000,
        D: 500,
        H: 20,
        L: 1500,
        fi: 20,
        latitude: null,
        longtitude: null,
        coords: null,
        secondPoint: null,
        color: '#54b0a2',
        fillColor: '#83e3d4',
      },
      DevDotFormData: {
        id: null,
        objId: 3,
        userId: null,
        h: 1000,
        r: 20,
        latitude: null,
        longtitude: null,
        color: '#473c36',
        fillColor: '#2f2d24',
      },
      DevRouteFormData: {
        id: null,
        objId: 4,
        userId: null,
        h: null,
        polyline: [],
        latitude: null,
        longtitude: null,
        color: '#2f3534',
      },

      /* отслеживание различных кликов */
      click: false,                 //  открытие панели с координатами
      clickCounter: 0,              //
      clickCoordinate: false,       //  отправка клика для выбора направления
      closeForm: false,             //  отслеживание состояния панели объектов
      modalCounter: 1,             //  счётчик показа модального окна


      modals: null,                 //  для вывода текста модального окна

      role: null,                   // сохранение роли пользователя
      valueA: null,                 // сохранение промежуточного значения для КРМ
    };
  },
  methods: {
    sendDataToMap() {
      if (this.clickCounter === 0 && this.selected !== 2) {
        this.showModal(1)
      } else {
        if (this.selected === 1) {
          if (this.ArpFormData.h === null) {
            console.log('kek')
          } else {
            this.$emit('sendObjectsInformation', this.selected, this.ArpFormData);
            this.resetForm();
          }
        } else if (this.selected === 2) {
          if (this.clickCoordinate === false) {
            this.showModal(2)
          } else if (this.GrmFormData.fi === null) {
            this.showModal(3)
          } else if (this.GrmFormData.fi > 90) {
            this.showModal(5)
          } else {
            this.$emit('sendObjectsInformation', this.selected, this.GrmFormData);
            this.resetForm();
          }
        } else if (this.selected === 3) {
          if (this.clickCounter === false) {
            this.showModal(1)
          } else if (this.DevDotFormData.h === null) {
            console.log('kek')
          } else {
            this.$emit('sendObjectsInformation', this.selected, this.DevDotFormData);
            this.resetForm();
          }
        } else if (this.selected === 4) {
          if (this.DevRouteFormData.h === null) {
            console.log('kek')
          } else {
            this.$emit('sendObjectsInformation', this.selected, this.DevRouteFormData);
            this.resetForm();
          }
        }
      }
    },
    sendClickOnCoordinateButton() {
      this.click = true;
      this.clickCounter = 1;
      if (this.selected !== 4) {
        this.$emit('sendChildClickOnCoordinateButton', this.click);
      } else {
        this.$emit('sendDevClickOnCoordinateButton', this.click);
      }

    },
    sendDirectionOnMap() {
      this.clickCoordinate = true;
      this.$emit('sendDirectionOnMap', this.clickCoordinate);
    },
    showModal(elem) {
      if (elem === 1) {
        this.modals = 'Укажите координаты объекта!'
      }
      if (elem === 2) {
        this.modals = 'Необходимо выбрать направление объекта!'
      }
      if (elem === 3) {
        this.modals = 'Необходимо указать угол!'
      }
      if (elem === 4) {
        this.modals = 'Необходимо указать высоту!'
      }
      if (elem === 5) {
        this.modals = 'Угол не должен быть больше 90°!'
      }
      this.$bvModal.show('modal-info');
      setTimeout(function () {
        this.$bvModal.hide('modal-info');
      }.bind(this), 2000);
    },
    updateRadius() {
      this.GrmFormData.r += this.GrmFormData.a - this.valueA;
      this.valueA = this.GrmFormData.a;
    },
    resetForm() {
      this.clickCounter = 0;
      this.clickCoordinate = false;
      this.closeForm = !this.closeForm;
    },
    deleteDevElem(index) {
      this.DevRouteFormData.polyline.splice(index, 1);
      this.$emit('updatePolylineArr', this.DevRouteFormData.polyline)
    }
  },
  created() {
    bus.$on('updateDevArr', data => {
      if (data) {
        this.DevRouteFormData.polyline.push(data)
        let arr = this.DevRouteFormData.polyline;
        if (arr[arr.length - 1] === arr[arr.length - 2]) {
          this.DevRouteFormData.polyline.pop(data)
        }

        this.clickCoordinate = true
      }
    })
  },
  mounted() {
    this.role = localStorage.getItem('role');
    this.valueA = this.GrmFormData.a;
    this.GrmFormData.r += this.GrmFormData.a;
  },
  updated() {
    this.role = localStorage.getItem('role');
    if (this.selected === 4 && this.modalCounter === 1) {
      this.$bvModal.show('modalDevRoute');
      this.modalCounter++
    }
  },
};
</script>

<style scoped>
h6 {
  font-size: .9rem !important;
}

.object-panel-container__title-container .text-warning {
  color: #fcf8f8 !important;
}

.object-panel-container__title-container .text-warning:hover {
  color: #dd757f !important;
}

.form-container__delete-button {
  outline: none;
  border: none;
  border-radius: 50%;
  margin-left: .4rem;
}

.form-container__delete-button .text-warning {
  color: rgba(0, 123, 255, 0.3) !important;
}

.form-container__delete-button .text-warning:hover {
  color: rgba(0, 123, 255, .8) !important;
}


.object-panel-container {
  width: 330px;
  background-color: rgba(255, 255, 255, 0.88);
  color: #495057;
  font-size: .9rem;
}

.object-panel-container__title-container {
  padding: .5rem;
  text-align: center;
  background-color: rgba(0, 123, 255, 0.3);
  display: flex;
  justify-content: center;
}

.object-panel-container__title {
  flex-grow: 3;
  align-self: center;
  text-align: start;
}

.object-panel-container__form-container {
  max-height: 600px;
  overflow-y: auto;
  padding: 1rem;
  height: auto;
}

.object-panel-container__select-container {
  display: flex;
}

#arpGroup, #grmGroup, #devGroup, #devRouteGroup {
  margin: 1rem;
}

.custom-select:hover {
  cursor: pointer !important;
}

.add-button {
  margin-top: 1rem;
  border-radius: 50rem;
  width: 100%;
}

.object-panel-container__close-button {
  background: none;
  outline: none;
  border: none;
}

.text-info {
  color: white !important;
}

.add-geo__container {
  display: flex;
  align-items: flex-end;
}

.location-button__arp {
  border-radius: 50rem;
  margin-left: .3rem;
}


.form-container__items {
  display: flex;
}

#grm_a, #grm_h, #grm_D, #grm_L {
  margin-right: .1rem;
}

#grm_b, #grm_r, #grm_H, #grm_Fi {
  margin-left: .1rem;
}

.coordinate-panel__inputs {
  display: flex;
  margin-top: 1rem;
}

@media (min-width: 1000px) and (max-width: 1390px) and (max-height: 900px) {
  .object-panel-container {
    width: 300px;
    font-size: .8rem;
  }

  input {
    padding: 0.375rem 0.75rem;
    height: calc(1.5em + 0.3rem);
  }

  .add-button {
    margin-top: .5rem;
  }

  .object-panel-container__form-container {
    max-height: 500px;
  }
}

@media (min-width: 600px) and (max-width: 780px) and (max-height: 1280px) {
  .object-panel-container {
    width: 270px;
    font-size: .8rem;
  }

  .object-panel-container__form-container {
    max-height: 400px;
  }
}

@media (min-width: 320px) and (max-width: 900px) and (max-height: 810px) {
  .object-panel-container {
    width: 270px;
    font-size: .8rem;
  }

  .object-panel-container__form-container {
    max-height: 400px;
  }
}
</style>
