<template>
  <div>
    <b-navbar toggleable="lg" type="dark">
      <!--      <router-link class="navbar-brand" to="/">🛠</router-link>-->
      <b-navbar-toggle target="nav-collapse" variant="dark"></b-navbar-toggle>
      <b-collapse id="nav-collapse" is-nav>
        <div>
          <router-link class="nav-link active" aria-current="page" to="/">Главная страница</router-link>
        </div>
        <!-- Right aligned nav items -->
        <b-navbar-nav class="ml-auto" v-if="showLink || userLogin === undefined || userLogin === null">
          <router-link class="nav-link justify-content-end" to="">
            <b-button pill v-b-modal.modal-signin variant="outline-primary" id="signInBtn">Вход</b-button>
          </router-link>
          <router-link class="nav-link justify-content-end" to="">
            <b-button pill v-b-modal.modal-signup variant="outline-dark">Регистрация</b-button>
          </router-link>
        </b-navbar-nav>
        <b-navbar-nav class="ml-auto" v-if="showLink === false && userLogin !== null">
          <div class="userInf">
            <router-link class="nav-link active" aria-current="page" to="/">Здравствуйте, <strong>{{
                userLogin
              }}</strong>
            </router-link>
          </div>
          <router-link class="nav-link justify-content-end" to="">
            <b-button pill variant="outline-dark" @click="resetUserLogin">Выйти</b-button>
          </router-link>
        </b-navbar-nav>
      </b-collapse>
    </b-navbar>
    <b-modal id="modal-signin" centered title="Вход" :hide-footer="true">
      <SignIn
          @sendResetShowOnPage="resetSignInFormOnPage"
      />
    </b-modal>
    <b-modal id="modal-signup" centered title="Регистрация" :hide-footer="true">
      <SignUp
          @sendResetRegistrationOnPage="resetSignUpFormOnPage"
      />
    </b-modal>
  </div>
</template>

<script>
import SignIn from '@/views/SignIn';
import SignUp from '@/views/SignUp';
import {bus} from '@/main';

export default {
  name: 'Header',
  components: {
    SignUp,
    SignIn,
    // eslint-disable-next-line vue/no-unused-components
    bus,
  },
  data() {
    return {
      showLink: true,
      userLogin: null,
    };
  },
  methods: {
    resetSignInFormOnPage() {
      this.$bvModal.hide('modal-signin');
      this.getUserLogin();
    },
    resetSignUpFormOnPage(elem) {
      if (elem === false) {
        this.$bvModal.hide('modal-signup');
        this.$bvModal.show('modal-signin');
        console.log(this.userLogin);
        this.getUserLogin();
      }
    },
    getUserLogin() {
      this.userLogin = localStorage.getItem('login');
      this.showLink = false;
    },
    resetUserLogin() {
      localStorage.removeItem('login');
      localStorage.removeItem('role');
      localStorage.removeItem('id');
      this.showLink = true;
    },
  },
  mounted() {
    this.userLogin = localStorage.getItem('login');
    if (this.userLogin !== null) {
      this.showLink = false;
    }
  },
  updated() {
    console.log(bus);
    bus.$emit('updateEvent', true);
  },
};
</script>

<style scoped>
.navbar {
  padding: 1.75rem !important;
  box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
}

.navbar-toggler {
  border-color: #838383 !important;
  background-color: #4a4949;
}

a {
  color: #282626 !important;
}

a:hover {
  color: #282626 !important;
}

.active {
  font-weight: 500 !important;
  font-size: 1rem;
  color: #171718 !important;
  padding-left: 0 !important;
  text-transform: uppercase;
  /*  border: 1.2px solid #007bff !important;*/
}

.active:hover {
  color: #3f3f40 !important;
}

.navbar-brand {
  font-weight: 700 !important;
}

#signInBtn {
  padding: 6px 42px !important;
}

.userInf {
  display: flex;
  align-items: center;
}

@media (min-width: 1000px) and (max-width: 1390px) and (max-height: 900px) {
  .navbar {
    padding: .4rem !important;
    font-size: .8rem;
  }

  .active {
    font-size: .8rem;
  }
}

@media (min-width: 320px) and (max-width: 900px) and (max-height: 810px) {
  .navbar {
    padding: .4rem !important;
    font-size: .8rem;
  }

  .active {
    font-size: .8rem;
  }
}
</style>
