<template>
  <div class="map-container">
    <div class="map__modal"></div>
    <l-map
        ref="map"
        :zoom="zoom"
        :center="center"
        @update:zoom="zoomUpdated"
        @update:center="centerUpdated"
        @update:bounds="boundsUpdated"
        @click="clickHandler"
    >
      <l-control position="bottomleft" class="navigation-panel" v-if="userRole !== null">
        <div class="navigation-panel__button-container">
          <button class="navigation-panel__nav-button"
                  v-b-popover.hover.top="'Внести изменения'"
                  @click="navigationPanelClick = !navigationPanelClick">
            <b-icon icon="pencil" font-scale="2.7" variant="info"></b-icon>
          </button>
          <div class="navigation-panel__arrow-button"
               @click="navigationPanelClick = true"
               v-if="navigationPanelClick === false">
            <b-icon icon="caret-right" animation="" font-scale="1" variant="info"></b-icon>
          </div>
          <div class="control_panel__group-container"
               id="panel-collapse"
               v-if="navigationPanelClick">
            <button class="control_panel__extendable-button"
                    v-b-popover.hover.top="'Добавить новый объект'"
                    @click="changeObjectsPanelVisible">
              <b-icon icon="plus-circle" font-scale="2.7" variant="info"></b-icon>
            </button>
            <button class="control_panel__extendable-button"
                    v-b-popover.hover.top="'Изменить объект'"
                    @click="editBtn = !editBtn"
                    :class="{ active: editBtn}"
                    v-b-toggle.sidebar-right>
              <b-icon icon="pencil-square" font-scale="2.7" variant="info"></b-icon>
            </button>
            <button class="control_panel__extendable-button"
                    v-b-popover.hover.top="'Удалить объект'"
                    @click="deleteBtn = !deleteBtn"
                    :class="{ active: deleteBtn}">
              <b-icon icon="trash" font-scale="2.7" variant="info"></b-icon>
            </button>
            <div class="navigation-panel__arrow-button control_panel__extendable-button"
                 @click="navigationPanelClick = false">
              <b-icon icon="caret-left" animation="" font-scale="1" variant="info"></b-icon>
            </div>
          </div>
        </div>
      </l-control>

      <l-control position="topright">
        <MapObjectsPanel
            @sendObjectsInformation="showChildOnMap"
            @sendDirectionOnMap="showDirectionLineOnMap"
            @sendChildClickOnCoordinateButton="showChildCoordinateOnMap"
            @sendDevClickOnCoordinateButton="tubeCoordinate = true"
            @updatePolylineArr="updatePolArr"
            v-if="openAddPanel && !deleteBtn && !openTubePanel"
        />
        <div v-if="openTubePanel">
          <div class="coordinate-panel">
            <h6 class="coordinate-panel__title">Застройщик приаэродномной зоны</h6>
            <button class="coordinate-panel__close-button"
                    @click="openCoordinatePanel = false">
              <b-icon icon="x-circle-fill"
                      font-scale="1.8"
                      variant="warning">
              </b-icon>
            </button>
          </div>
          <div class="tube-panel__container">
            <b-form class="tube-panel__form-container"
                    @submit.prevent>
              <b-button type="button"
                        variant="primary"
                        class="location-button__arp w-25"
                        v-b-popover.hover.top="'Указать координаты объекта'"

              >
                <b-icon icon="geo-alt" font-scale="1.5" variant="info" animation="cylon-vertical"
                        v-if="tubeDataDev.length === 0"></b-icon>
                <b-icon icon="geo-alt" font-scale="1.5" variant="info" v-else></b-icon>
              </b-button>
            </b-form>
          </div>

          <l-polyline
              v-if="tubeDataDev.length !== 0"
              :lat-lngs="tubeDataDev"
              :color="polyline.color">
          </l-polyline>
        </div>
      </l-control>

      <l-control v-show="openCoordinatePanel" position="topleft">
        <div class="coordinate-panel">
          <h6 class="coordinate-panel__title">Панель координат</h6>
          <button class="coordinate-panel__close-button" @click="openCoordinatePanel = false">
            <b-icon icon="x-circle-fill" font-scale="1.8" variant="warning"></b-icon>
          </button>
        </div>
        <b-form class="coordinate-panel__form">
          <b-form-group label="Отметьте на карте или введите координаты:" label-for="coordinate1">
            <div class="coordinate-panel__inputs">
              <b-form-input
                  class="w-50"
                  id="coordinate1"
                  v-model.number="latitude"
                  type="number"
                  placeholder="Введите lat"
                  min="1"
                  step="0.000001"
              ></b-form-input>
              <b-form-input
                  class="w-50"
                  id="coordinate2"
                  v-model.number="longtitude"
                  type="number"
                  placeholder="Введите lon"
                  min="1"
                  step="0.000001"
              ></b-form-input>
            </div>
          </b-form-group>
          <b-button type="button" variant="primary" class="add-button__coordinate" @click="clickOnCoordinatePanelBtn">
            Подтвердить
          </b-button>
        </b-form>

      </l-control>

      <!--      <l-geo-json :geojson="geojson"></l-geo-json>
            <l-draw-toolbar v-if="openAddPanel2"></l-draw-toolbar>-->

      <div v-for="info in circleDataARP" :key="info.id">
        <div>
          <l-circle
              :lat-lng='[info.latitude, info.longtitude]'
              :radius="info.r"
              :color="info.color"
              :fillColor="info.fillColor"
          />
          <l-circle
              :lat-lng="[info.latitude, info.longtitude]"
              :radius="info.j"
              :color="info.color"
              :fillColor="info.fillColor"
              @click="editChildOnMap(info)"
          />
        </div>
      </div>

      <div v-for="info in polygonDataGRM" :key="info.id">
        <l-polygon
            :lat-lngs="info.coords"
            :color="info.color"
            @click="editChildOnMap(info)"
        >
        </l-polygon>
      </div>
      <l-polyline
          v-if="polyline.showPolyline"
          :lat-lngs="polyline.latlngs"
          :color="polyline.color"
          :weight='10'>
      </l-polyline>

      <l-polyline
          v-for="info in tubeDataDev" :key="info.id"
          :lat-lngs="info.polyline"
          :color="info.color"
          :weight='10'
          @click="editChildOnMap(info)">
      </l-polyline>


      <div v-for="info in circleDataDEV" :key="info.id">
        <l-circle
            :lat-lng='[info.latitude, info.longtitude]'
            :radius="info.radius"
            :color="info.color"
            :fillColor="info.fillColor"
            @click="editChildOnMap(info)"
        />
      </div>
      <l-tile-layer :url="url"></l-tile-layer>
    </l-map>

    <div>
      <b-modal id="modalInfoOnMap" hide-backdrop content-class="shadow" hide-header hide-footer centered>
        <p class="my-2">{{ modalText }}</p>
      </b-modal>
    </div>

    <div>
      <b-modal id="modalInfoDirection" content-class="shadow" hide-header hide-footer centered>
        <p class="my-2">Укажите две точки для выбора направления</p>
        <b-button class="mt-3" block @click="$bvModal.hide('modalInfoDirection')">Всё понятно!</b-button>
      </b-modal>
    </div>
    <div>
      <b-modal id="modalInfoOnMap" hide-backdrop content-class="shadow" hide-header hide-footer centered>
        <p class="my-2">{{ modalText }}</p>
      </b-modal>
    </div>
    <div>
      <b-modal id="modalHeightAlert" hide-backdrop content-class="shadow" hide-header hide-footer centered>
        <p class="my-4">{{ modalText }}</p>
        <b-button class="mt-3" variant="outline-danger" block @click="$bvModal.hide('modalHeightAlert')">Okay</b-button>
      </b-modal>
    </div>

    <div v-show="changeInfo">
      <b-sidebar id="sidebar-right" right shadow no-header-close>
        <div class="px-3 py-2">
          <h5><strong>Внести изменения:</strong></h5>
          <hr>
          <b-button type="button" variant="primary" class="location-button__arp w-25"
                    v-b-popover.hover.top="'Указать местоположение объекта'"
                    v-if="this.arrType !== 2 && this.arrType !== 4"
                    @click="showChildCoordinateOnMap(true)">
            <b-icon icon="geo-alt" font-scale="1.5" variant="info"></b-icon>
          </b-button>
          <b-button type="button" variant="primary" class="location-button__arp w-25"
                    v-b-popover.hover.top="'Выбор направления объекта'" v-if="this.arrType === 2"
                    @click="showDirectionLineOnMap">
            <b-icon icon="arrow-up-right" font-scale="1.5" variant="info"></b-icon>
          </b-button>


          <div v-if="this.arrType !== 4">
            <div v-for="(info, index) in changeInfo" :key="index">
              <p>{{ info[0] }}</p>
              <b-form-input
                  id="objectHeight"
                  v-model.number="info[1]"
                  type="number"
                  placeholder="Введите новые данные"
                  min="1"
              ></b-form-input>
              <hr>
            </div>
          </div>

          <div v-if="this.arrType === 4">
            <p>{{ changeInfo[0] }}</p>
            <b-form-input
                id="objectHeight"
                v-model.number="changeInfo[1]"
                type="number"
                placeholder="Введите новые данные"
                min="1"
            ></b-form-input>
            <hr>
            <p>Координаты объекта:</p>
            <div v-for="(info, index) in changePolyline" :key="index">
              <div class="coord-container">
                <b-form-input
                    id="objectHeight"
                    v-model.number="info[0]"
                    type="number"
                    placeholder="Введите новые данные"
                    min="1"
                ></b-form-input>
                <b-form-input
                    id="objectHeight"
                    v-model.number="info[1]"
                    type="number"
                    placeholder="Введите новые данные"
                    min="1"
                ></b-form-input>
                <button type="button" class="delete-coord_button" @click="changePolyline.splice(index, 1);">
                  <b-icon icon="x-circle-fill" font-scale="1.8" variant="warning"></b-icon>
                </button>
              </div>
              <hr>
            </div>
          </div>
          <b-button type="submit" variant="primary" class="add-button"
                    @click="saveEditInfo">
            Сохранить изменения
          </b-button>
        </div>
      </b-sidebar>
    </div>
    <!--    <button @click="test">test</button>-->
    <!--    <pre>{{ $data }}</pre>-->
  </div>
</template>

<script>
import {LMap, LTileLayer, LCircle, LControl, LPolygon, LPolyline} from 'vue2-leaflet';
import MapObjectsPanel from '@/components/MapObjectsPanel';
import {HTTP} from '@/main';
import {bus} from '@/main';

export default {
  name: 'LeafletMap',
  props: {},
  components: {
    MapObjectsPanel,
    // eslint-disable-next-line vue/no-unused-components
    bus,
    LMap,
    LTileLayer,
    LCircle,
    LControl,
    LPolygon,
    LPolyline,
  },
  data() {
    return {
      /* для leaflet*/
      url: 'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',
      zoom: 14,
      center: [59.798303, 30.280123],
      cursor: true,
      bounds: null,

      polyline: {                   // Отрисовка направления
        latlngs: [],
        color: '#54b0a2',
        showPolyline: false,
      },

      /* сохранение информации об объектах */
      circleDataARP: [],
      polygonDataGRM: [],
      circleDataDEV: [],
      tubeDataDev: [],

      /* открытие панелей и отслеживание нажатия кнопок */
      navigationPanelClick: false,  // панель навигации слева снизу
      openCoordinatePanel: false,   // панель координат слева сверху
      openAddPanel: false,          // панель добавления объектов
      openTubePanel: false,         // панель добавления труб-палок у застройщика
      editBtn: false,               // кнопка изменения объектов
      deleteBtn: false,             // кнопка удаления объектов
      pointCoordinate: false,       // отслеживание клика по карте для выбора направления антенны
      heightAlert: false,           // вывод о пересаечении БРА
      tubeCoordinate: false,

      /* сохранение координат из панели координат */
      latitude: null,
      longtitude: null,
      userRole: null,               // сохранение роли пользователя
      userName: null,               // сохранение логина пользователя
      userId: null,                 // сохранение индификатора пользоавателя для отрисовки объектов
      modalText: '',                // заготовка для текста модалки
      errors: [],                   // отлов ошибок

      /* Вывод информации для изменения в сайдбаре */
      changeInfo: null,
      changePolyline: null,
      arrIdx: null,
      arrType: null,
    };
  },
  methods: {
    zoomUpdated(zoom) {
      this.zoom = zoom;
    },
    centerUpdated(center) {
      this.center = center;
    },
    boundsUpdated(bounds) {
      this.bounds = bounds;
    },
    clickHandler(elem) {  /* отслеживание клика по карте для нахождения координат */
      if (this.openCoordinatePanel === true) {
        this.latitude = elem.latlng.lat.toFixed(6);
        this.longtitude = elem.latlng.lng.toFixed(6);
      }
      if (this.pointCoordinate === true) {
        this.latitude = elem.latlng.lat;
        this.longtitude = elem.latlng.lng;
        this.polyline.showPolyline = true;

        let arr = [this.latitude, this.longtitude];

        if (this.polyline.latlngs.length < 2) {
          this.polyline.latlngs.push(arr);

          if (this.polyline.latlngs.length === 2) {
            this.pointCoordinate = false;
            this.$bvModal.hide('modalInfoDirection');
            this.showModalInformation(4);

            this.latitude = this.polyline.latlngs[0][0];
            this.longtitude = this.polyline.latlngs[0][1];
          }
        }
      }
      if (this.tubeCoordinate === true) {
        // eslint-disable-next-line no-unused-vars
        const color = '#54b0a2';
        this.latitude = elem.latlng.lat.toFixed(4);
        this.longtitude = elem.latlng.lng.toFixed(4);
        this.polyline.showPolyline = true;
        let arr = [this.latitude, this.longtitude];
        this.polyline.color = '#817f7f';
        this.polyline.latlngs.push(arr);

        bus.$emit('updateDevArr', arr);
      }
    },

    updatePolArr(data) {
      this.polyline.latlngs = data;
    },

    clickOnCoordinatePanelBtn() {  /* сохранение выбранных координат */
      if (this.latitude && this.longtitude !== null) {
        this.openCoordinatePanel = !this.openCoordinatePanel;
      } else {
        this.showModalInformation(3);
      }
    },

    async showChildOnMap(object_id, data) { /* отрисовка объектов на карте */
      let copy = {...data};
      if (this.latitude && this.longtitude !== null) {

        copy['latitude'] = this.latitude;
        copy['longtitude'] = this.longtitude;
        copy['userId'] = Number(this.userId);

        /* Нахождение координат для постройки КРМ */
        if (object_id === 2) { /* КРМ */
          copy['secondPoint'] = this.polyline.latlngs[1];
          try {
            let response = await HTTP.post(`/bra_coords/`, {
              'latitude': copy['latitude'],
              'longtitude': copy['longtitude'],
              'second_point': copy['secondPoint'],
              'params': copy,
            });
            console.log(response);
            copy['coords'] = response.data['coords'];

          } catch (e) {
            this.errors.push(e);
          }
        }

        if (object_id !== 3 && object_id !== 4) {
          /* Сохранение объектов пользователя в базу */
          try {
            let responseObjId = await HTTP.post('/add_user_object/',
                {
                  'user_login': localStorage.getItem('login'),
                  'object_id': object_id,
                  'params': copy,
                });
            copy['id'] = responseObjId.data['uo_id'];
          } catch (e) {
            this.errors.push(e);
          }
        }

        if (object_id === 1) { /* АРП */
          this.circleDataARP.push(copy);
          this.showModalInformation(1);
        }
        if (object_id === 2) { /* КРМ */
          this.polyline.latlngs = null;
          this.polyline.showPolyline = false;
          this.polygonDataGRM.push(copy);
          this.showModalInformation(1);
          this.polyline.latlngs = [];
        }
        if (object_id === 3) { /* Застройщик точка */
          copy['radius'] = 150;
          let answer = await this.calculateIntersection(copy['h'], copy['objId']);
          if (answer === 200) {
            try {
              let responseObjId = await HTTP.post('/add_user_object/',
                  {
                    'user_login': localStorage.getItem('login'),
                    'object_id': object_id,
                    'params': copy,
                  });
              copy['id'] = responseObjId.data['uo_id'];
            } catch (e) {
              this.errors.push(e);
            }
            this.circleDataDEV.push(copy);
            this.showModalInformation(1);

          } else if (answer['height_error'] === 0) {
            this.modalText = 'Переместите объект от центра БРА';
            this.$bvModal.show('modalHeightAlert');
            this.changeIntersectionColor(this.circleDataARP, this.polygonDataGRM, answer);
          } else if (answer['height_error'] !== 0) {
            this.modalText = `Переместите объект от центра БРА или уменьшите высоту на ${answer['height_error'].toFixed(
                1)}`;
            this.$bvModal.show('modalHeightAlert');
            this.changeIntersectionColor(this.circleDataARP, this.polygonDataGRM, answer);
          }
        }
        if (object_id === 4) {
          copy['latitude'] = this.polyline.latlngs[0][0];
          copy['longtitude'] = this.polyline.latlngs[0][1];
          copy['polyline'] = this.polyline.latlngs;
          this.polyline.latlngs = [];
          let answer = await this.calculateIntersection(copy['h'], copy['objId'], copy['polyline']);
          if (answer === 200) {
            try {
              let responseObjId = await HTTP.post('/add_user_object/',
                  {
                    'user_login': localStorage.getItem('login'),
                    'object_id': object_id,
                    'params': copy,
                  });
              copy['id'] = responseObjId.data['uo_id'];
            } catch (e) {
              this.errors.push(e);
            }
            this.tubeDataDev.push(copy);
            this.tubeCoordinate = false;
            this.showModalInformation(1);
          } else if (answer['height_error'] === 0) {
            this.modalText = 'Переместите объект от центра БРА';
            this.$bvModal.show('modalHeightAlert');
            this.changeIntersectionColor(this.circleDataARP, this.polygonDataGRM, answer);
          } else if (answer['height_error'] !== 0) {
            this.modalText = `Переместите объект от центра БРА или уменьшите высоту на ${answer['height_error'].toFixed(
                1)}`;
            this.$bvModal.show('modalHeightAlert');
            this.changeIntersectionColor(this.circleDataARP, this.polygonDataGRM, answer);
          }
        }
        this.latitude = null;
        this.longtitude = null;
        this.openCoordinatePanel = false;
        this.changeObjectsPanelVisible();
      }
    },

    changeIntersectionColor(ARPArr, GRMArr, answer) { /* Смена цвета объекта при пересечении */
      for (let i = 0; i < ARPArr.length; i++) {
        if (ARPArr[i]['id'] === answer['uo_id']) {
          let color = ARPArr[i]['color'];
          let fillColor = ARPArr[i]['fillColor'];
          ARPArr[i]['color'] = '#b05457';
          ARPArr[i]['fillColor'] = '#e07f82';
          setTimeout(function () {
            ARPArr[i]['color'] = color;
            ARPArr[i]['fillColor'] = fillColor;
          }.bind(this), 2000);
          return;
        }
      }
      for (let i = 0; i < GRMArr.length; i++) {
        if (GRMArr[i]['id'] === answer['uo_id']) {
          let color = GRMArr[i]['color'];
          let fillColor = GRMArr[i]['fillColor'];
          GRMArr[i]['color'] = '#b05457';
          GRMArr[i]['fillColor'] = '#e07f82';
          setTimeout(function () {
            GRMArr[i]['color'] = color;
            GRMArr[i]['fillColor'] = fillColor;
          }.bind(this), 2000);
          return;
        }
      }
    },

    async calculateIntersection(height, type, point_list = []) { /* Пересечение трубы застройщика */
      try {
        let response = await HTTP.post(`/bra/`, {
          'latitude': this.latitude,
          'longtitude': this.longtitude,
          'height': height,
          'object_type': type,
          'points_list': point_list,
        });
        return response.status;
      } catch (e) {
        this.errors.push(e);
        return e.response.data;
      }
    },

    async calculateIntersectionForEdit(lat, lon, height, type, point_list = []) { /* Пересечение трубы застройщика */
      try {
        let response = await HTTP.post(`/bra/`, {
          'latitude': lat,
          'longtitude': lon,
          'height': height,
          'object_type': type,
          'points_list': point_list,
        });
        return response.status;
      } catch (e) {
        this.errors.push(e);
        return e.response.data;
      }
    },

    showChildCoordinateOnMap(elem) { /* открытие панели выбора координат */
      if (elem === true) {
        this.openCoordinatePanel = true;
      }
    },

    async editChildOnMap(childId) { /* Удаление объекта с карты */
      if (this.deleteBtn === true && this.editBtn === true) {
        this.modalText = 'Необходимо выбрать один режим изменения объекта';
        this.$bvModal.show('modalHeightAlert');
      }
      if (this.deleteBtn === true && this.editBtn === false) {
        this.modalText = '';
        if (childId['objId'] === 1 && this.userRole === '1') {
          await this.deleteObjectOnMap(childId, this.circleDataARP)
        }
        if (childId['objId'] === 2 && this.userRole === '1') {
          await this.deleteObjectOnMap(childId, this.polygonDataGRM)
        }
        if (childId['objId'] === 3 && this.userRole === '2') {
          await this.deleteObjectOnMap(childId, this.circleDataDEV)
        }
        if (childId['objId'] === 4 && this.userRole === '2') {
          await this.deleteObjectOnMap(childId, this.tubeDataDev)
        }
      }
      if (this.deleteBtn === false && this.editBtn === true) {
        this.modalText = '';
        if (childId['objId'] === 1 && this.userRole === '1') {
          for (let i = 0; i < this.circleDataARP.length; i++) {
            if (this.circleDataARP[i]['id'] === childId['id'] && this.circleDataARP[i]['userId'] === Number(this.userId)) {
              this.prevCenter = this.center;
              this.center = [Number(this.circleDataARP[i]['latitude']), Number(this.circleDataARP[i]['longtitude'])]
              this.changeInfo = [
                ['Радиус цилиндра r (м):', this.circleDataARP[i]['r']],
                ['Угол α (°):', this.circleDataARP[i]['a']],
                ['Радиус конуса R (м):', this.circleDataARP[i]['R']],
                ['Радиус цилиндра j (м):', this.circleDataARP[i]['j']],
                ['Высота цилиндра j (м):', this.circleDataARP[i]['h']],
              ]
              this.arrIdx = i;
              this.arrType = this.circleDataARP[i]['objId']
            }
          }
        }
        if (childId['objId'] === 2 && this.userRole === '1') {
          for (let i = 0; i < this.polygonDataGRM.length; i++) {
            if (this.polygonDataGRM[i]['id'] === childId['id'] && this.polygonDataGRM[i]['userId'] ===
                Number(this.userId)) {
              this.center = [Number(this.polygonDataGRM[i]['latitude']), Number(this.polygonDataGRM[i]['longtitude'])];
              this.changeInfo = [
                ['a (м):', this.polygonDataGRM[i]['a']],
                ['b (м):', this.polygonDataGRM[i]['b']],
                ['h (м):', this.polygonDataGRM[i]['h']],
                ['D (м):', this.polygonDataGRM[i]['D']],
                ['H (м):', this.polygonDataGRM[i]['H']],
                ['L (м):', this.polygonDataGRM[i]['L']],
                ['φ (°):', this.polygonDataGRM[i]['fi']],
              ]
              this.arrIdx = i;
              this.arrType = this.polygonDataGRM[i]['objId']
              break;
            }
          }
        }
        if (childId['objId'] === 3 && this.userRole === '2') {
          for (let i = 0; i < this.circleDataDEV.length; i++) {
            if (this.circleDataDEV[i]['id'] === childId['id'] && this.circleDataDEV[i]['userId'] ===
                Number(this.userId)) {
              this.center = [Number(this.circleDataDEV[i]['latitude']), Number(this.circleDataDEV[i]['longtitude'])];
              this.changeInfo = [
                ['Высота объекта (относительная, в м):', this.circleDataDEV[i]['h']],
              ]
              this.arrIdx = i;
              this.arrType = this.circleDataDEV[i]['objId'];
            }
          }
        }
        if (childId['objId'] === 4 && this.userRole === '2') {
          console.log(childId['id'])
          for (let i = 0; i < this.circleDataDEV.length; i++) {
            if (this.tubeDataDev[i]['id'] === childId['id'] && this.tubeDataDev[i]['userId'] ===
                Number(this.userId)) {
              this.center = [Number(this.tubeDataDev[i]['latitude']), Number(this.tubeDataDev[i]['longtitude'])]
              this.changeInfo = ['Высота объекта (относительная, в м):', this.tubeDataDev[i]['h']];
              this.changePolyline = this.tubeDataDev[i]['polyline'];
              this.arrIdx = i;
              this.arrType = this.tubeDataDev[i]['objId']
            }
          }
        }
      }
    },

    async saveEditInfo() {
      if (this.arrType === 1) {
        this.circleDataARP[this.arrIdx]['r'] = this.changeInfo[0][1];
        this.circleDataARP[this.arrIdx]['a'] = this.changeInfo[1][1];
        this.circleDataARP[this.arrIdx]['R'] = this.changeInfo[2][1];
        this.circleDataARP[this.arrIdx]['j'] = this.changeInfo[3][1];
        this.circleDataARP[this.arrIdx]['h'] = this.changeInfo[4][1];

        if (this.latitude !== null && this.longtitude !== null) {
          this.circleDataARP[this.arrIdx]['latitude'] = this.latitude;
          this.circleDataARP[this.arrIdx]['longtitude'] = this.longtitude;
        }

        try {
          let resp = await HTTP.post('/update_map_object/', {
            'user_login': localStorage.getItem('login'),
            'object_id': this.circleDataARP[this.arrIdx]['id'],
            'params': this.circleDataARP[this.arrIdx]
          });
          if (resp.status === 200) {
            this.showModalInformation(2)
          }
        } catch (e) {
          this.errors.push(e);
        }
      }
      if (this.arrType === 2) {
        this.polygonDataGRM[this.arrIdx]['a'] = this.changeInfo[0][1];
        this.polygonDataGRM[this.arrIdx]['b'] = this.changeInfo[1][1];
        this.polygonDataGRM[this.arrIdx]['h'] = this.changeInfo[2][1];
        this.polygonDataGRM[this.arrIdx]['D'] = this.changeInfo[3][1];
        this.polygonDataGRM[this.arrIdx]['H'] = this.changeInfo[4][1];
        this.polygonDataGRM[this.arrIdx]['L'] = this.changeInfo[5][1];

        if (this.latitude !== null && this.longtitude !== null) {
          this.polygonDataGRM[this.arrIdx]['latitude'] = this.latitude;
          this.polygonDataGRM[this.arrIdx]['longtitude'] = this.longtitude;
          this.polygonDataGRM[this.arrIdx]['fi'] = this.changeInfo[6][1];

          try {
            let response = await HTTP.post(`/bra_coords/`, {
              'latitude': this.latitude,
              'longtitude': this.longtitude,
              'second_point': this.polyline.latlngs[1],
              'params': this.polygonDataGRM[this.arrIdx],
            });
            this.polygonDataGRM[this.arrIdx]['coords'] = response.data['coords'];
          } catch (e) {
            this.errors.push(e);
          }
        }

        if (this.polygonDataGRM[this.arrIdx]['fi'] !== this.changeInfo[6][1]) {
          this.polygonDataGRM[this.arrIdx]['fi'] = this.changeInfo[6][1];

          try {
            let response = await HTTP.post(`/bra_coords/`, {
              'latitude': this.polygonDataGRM[this.arrIdx]['latitude'],
              'longtitude': this.polygonDataGRM[this.arrIdx]['longtitude'],
              'second_point': this.polygonDataGRM[this.arrIdx]['secondPoint'],
              'params': this.polygonDataGRM[this.arrIdx],
            });
            this.polygonDataGRM[this.arrIdx]['coords'] = response.data['coords'];
          } catch (e) {
            this.errors.push(e);
          }
        }

        try {
          let resp = await HTTP.post('/update_map_object/', {
            'user_login': localStorage.getItem('login'),
            'object_id': this.polygonDataGRM[this.arrIdx]['id'],
            'params': this.polygonDataGRM[this.arrIdx]
          });
          if (resp.status === 200) {
            this.showModalInformation(2)
          }
        } catch (e) {
          this.errors.push(e);
        }
      }
      if (this.arrType === 3) {
        let answer;
        if (this.latitude !== null && this.longtitude !== null) {
          answer = await this.calculateIntersection(this.changeInfo[0][1], 3);
        }
        if (this.latitude === null && this.longtitude !== null) {
          answer = await this.calculateIntersectionForEdit(this.circleDataDEV[this.arrIdx]['latitude'], this.longtitude, this.changeInfo[0][1], 3);
        }
        if (this.latitude !== null && this.longtitude === null) {
          answer = await this.calculateIntersectionForEdit(this.latitude, this.circleDataDEV[this.arrIdx]['longtitude'], this.changeInfo[0][1], 3);
        }
        if (this.latitude === null && this.longtitude === null) {
          answer = await this.calculateIntersectionForEdit(this.circleDataDEV[this.arrIdx]['latitude'], this.circleDataDEV[this.arrIdx]['longtitude'], this.changeInfo[0][1], 3);
        }

        if (answer === 200) {
          this.circleDataDEV[this.arrIdx]['h'] = this.changeInfo[0][1];
          if (this.latitude !== null && this.longtitude !== null) {
            this.circleDataDEV[this.arrIdx]['latitude'] = this.latitude;
            this.circleDataDEV[this.arrIdx]['longtitude'] = this.longtitude;
          }
          if (this.latitude === null && this.longtitude !== null) {
            this.circleDataDEV[this.arrIdx]['longtitude'] = this.longtitude;
          }
          if (this.latitude !== null && this.longtitude === null) {
            this.circleDataDEV[this.arrIdx]['latitude'] = this.latitude;
          }
          try {
            let resp = await HTTP.post('/update_map_object/', {
              'user_login': localStorage.getItem('login'),
              'object_id': this.circleDataDEV[this.arrIdx]['id'],
              'params': this.circleDataDEV[this.arrIdx]
            });
            if (resp.status === 200) {
              this.showModalInformation(2)
            }
          } catch (e) {
            this.errors.push(e);
          }
        } else if (answer['height_error'] === 0) {
          this.modalText = 'Переместите объект от центра БРА';
          this.$bvModal.show('modalHeightAlert');
          this.changeIntersectionColor(this.circleDataARP, this.polygonDataGRM, answer);
          this.resetEditPanel()
        } else if (answer['height_error'] !== 0) {
          this.modalText = `Переместите объект от центра БРА или уменьшите высоту на ${answer['height_error'].toFixed(
              1)}`;
          this.$bvModal.show('modalHeightAlert');
          this.changeIntersectionColor(this.circleDataARP, this.polygonDataGRM, answer);
          this.resetEditPanel()
        }
      }
      if (this.arrType === 4) {
        let answer;
        if (this.changeInfo[0][1] !== null) {
          answer = await this.calculateIntersectionForEdit(this.tubeDataDev[this.arrIdx]['latitude'], this.tubeDataDev[this.arrIdx]['longtitude'], this.changeInfo[1], 3, this.tubeDataDev[this.arrIdx]['polyline']);
        } else {
          answer = await this.calculateIntersectionForEdit(this.tubeDataDev[this.arrIdx]['latitude'], this.tubeDataDev[this.arrIdx]['longtitude'], this.tubeDataDev[this.arrIdx]['h'], 3, this.tubeDataDev[this.arrIdx]['polyline']);
        }

        if (answer === 200) {
          this.tubeDataDev[this.arrIdx]['h'] = this.changeInfo[1];
          try {
            let resp = await HTTP.post('/update_map_object/', {
              'user_login': localStorage.getItem('login'),
              'object_id': this.tubeDataDev[this.arrIdx]['id'],
              'params': this.tubeDataDev[this.arrIdx]
            });
            if (resp.status === 200) {
              this.showModalInformation(2)
            }
          } catch (e) {
            this.errors.push(e);
          }
        } else if (answer['height_error'] === 0) {
          this.modalText = 'Переместите объект от центра БРА';
          this.$bvModal.show('modalHeightAlert');
          this.changeIntersectionColor(this.circleDataARP, this.polygonDataGRM, answer);
          this.resetEditPanel()
        } else if (answer['height_error'] !== 0) {
          this.modalText = `Переместите объект от центра БРА или уменьшите высоту на ${answer['height_error'].toFixed(
              1)}`;
          this.$bvModal.show('modalHeightAlert');
          this.changeIntersectionColor(this.circleDataARP, this.polygonDataGRM, answer);
          this.resetEditPanel()
        }
      }
      this.resetEditPanel()
    },

    resetEditPanel() {
      this.changeInfo = null;
      this.changePolyline = null;
      this.arrIdx = null;
      this.arrType = null;

      this.latitude = null;
      this.longtitude = null;

      this.polyline.latlngs = [];
      this.polyline.showPolyline = false;
    },

    async deleteObjectOnMap(childId, obj) {
      for (let i = 0; i < obj.length; i++) {
        if (obj[i]['id'] === childId['id'] && obj[i]['userId'] ===
            Number(this.userId)) {
          try {
            let response = await HTTP.post(`/delete_map_object/`, {
              'user_login': this.userName,
              'object_id': obj[i]['id'],
              'params': obj[i],
            });
            if (response.status === 200) {
              obj[i]['color'] = '#b05457';
              obj[i]['fillColor'] = '#e07f82';
              this.showModalInformation(5);
              setTimeout(function () {
                obj.splice(i, 1);
              }.bind(this), 2000);
            } else {
              this.showModalInformation(6);
            }
          } catch (e) {
            this.errors.push(e);
          }
          break;
        }
      }
    },

    showModalInformation(elem) {
      if (elem === 1) {
        this.modalText = 'Ваш объект успешно добавлен!';
      }
      if (elem === 2) {
        this.modalText = 'Объект успешно изменён!';
      }
      if (elem === 3) {
        this.modalText = 'Введите координаты объекта';
      }
      if (elem === 4) {
        this.modalText = 'Направление сохранено';
      }
      if (elem === 5) {
        this.modalText = 'Объект удалён!';
      }
      if (elem === 6) {
        this.modalText = 'Объект не найден!';
      }
      this.$bvModal.show('modalInfoOnMap');
      setTimeout(function () {
        this.$bvModal.hide('modalInfoOnMap');
      }.bind(this), 2000);
    },

    changeObjectsPanelVisible() {
      this.openAddPanel = !this.openAddPanel;
    },

    showDirectionLineOnMap() {
      this.pointCoordinate = true;
      this.$bvModal.show('modalInfoDirection');
    },

    async loadUserObjects() {
      try {
        let response = await HTTP.get(`/map_objects/`);
        if (response.status === 200) {
          let arr = response.data['objects'];
          console.log(response.data['objects']);
          for (let i = 0; i < arr.length; i++) {
            let obj = JSON.parse(arr[i][5]);
            obj['id'] = arr[i][0];
            if (obj['objId'] === 1) {
              if (this.userId !== String(arr[i][1])) {
                obj['color'] = '#149AEB';
                obj['fillColor'] = '#0217E8';
              }
              this.circleDataARP.push(obj);
            }
            if (obj['objId'] === 2) {
              if (this.userId !== String(arr[i][1])) {
                obj['color'] = '#149AEB';
              }
              this.polygonDataGRM.push(obj);
            }
            if (obj['objId'] === 3) {
              if (this.userId !== String(arr[i][1])) {
                obj['color'] = '#149AEB';
                obj['fillColor'] = '#0217E8';
              }
              this.circleDataDEV.push(obj);
            }
            if (obj['objId'] === 4) {
              if (this.userId !== String(arr[i][1])) {
                obj['color'] = '#149AEB';
                obj['fillColor'] = '#0217E8';
              }
              this.tubeDataDev.push(obj);
            }
          }
        }
      } catch (e) {
        this.errors.push(e);
        console.log(this.errors);
      }
    }
    ,
  },
  created() {
    this.userRole = localStorage.getItem('role');
    this.userId = localStorage.getItem('id');
    this.userName = localStorage.getItem('login');

    if (this.userRole) {
      this.loadUserObjects();
    }
  },
  beforeMount() {

  },
  mounted() {

  },
  updated() {

  },
};
</script>

