<template>
  <div class="signin-container">
    <b-spinner variant="primary" label="Spinning" v-if="download"></b-spinner>
    <b-form @reset="onReset" v-if="show" class="signin-container__form justify-content-center">
      <b-form-group id="inputGroupSignIn1" label="Логин:" label-for="inputSignIn1" required>
        <b-form-input
            id="inputSignIn1"
            v-model="form.login"
            type="text"
            placeholder="Введите логин"
            minlength="3"
            maxlenght="32"
            autofocus
            required
        ></b-form-input>
      </b-form-group>

      <b-form-group id="inputGroupSignIn2" label="Ваш пароль:" label-for="inputSignIn2" required>
        <b-form-input
            id="inputSignIn2"
            v-model="form.password"
            type="password"
            placeholder="Введите пароль"
            minlength="4"
            maxlenght="32"
            required
        ></b-form-input>
      </b-form-group>

      <b-button type="button" variant="primary" class="signin-container__submit" @click.prevent="onSubmit">Войти
      </b-button>
    </b-form>
    <div>
      <b-modal id="modal-login" content-class="shadow" hide-header hide-footer centered>
        <p class="my-2">
          Введён неверный логин или пароль!
        </p>
      </b-modal>
    </div>
  </div>
</template>

<script>
import {HTTP} from '@/main';

export default {
  name: 'SignIn',
  data() {
    return {
      form: {
        login: '',
        password: '',
      },
      show: true,
      download: false,
      errors: [],
    };
  },
  methods: {
    async onSubmit() {
      try {
        let response = await HTTP.post('/login/', this.form)
        if (response.status === 200) {
          localStorage.setItem('id', response.data['u_id']);
          localStorage.setItem('login', response.data['u_login']);
          localStorage.setItem('role', response.data['u_type']);
          this.onReset();
        }
      } catch (e) {
        this.$bvModal.show('modal-login');
        setTimeout(function () {
          this.$bvModal.hide('modal-login');
        }.bind(this), 2000);
        this.errors.push(e)
        console.log(this.errors)
      }
    },
    onReset() {
      this.download = true;
      this.form.login = '';
      this.form.password = '';

      this.show = false;

      setTimeout(function () {
        this.download = false;
        this.$emit('sendResetShowOnPage');
      }.bind(this), 100);
    },
  },
};
</script>

<style scoped>
.signin-container {
  margin: 1.5rem;
}

#inputSignIn1, #inputSignIn2 {
  border-radius: .9rem;
  border-color: white;
  background-image: linear-gradient(120deg, rgba(161, 196, 253, .4) 0%, rgba(194, 233, 251, .8) 100%);
  opacity: .7;
}

.signin-container__submit {
  margin-top: 1.5rem;
  width: 100%;
  text-align: center;
  background-image: linear-gradient(120deg, #484848bf 0%, #484848bf 100%);
  border: none;
  border-radius: .9rem;
  font-weight: 500;

}

.signin-container__submit:hover {
  background-image: linear-gradient(120deg, #d4d7da 0%, #d4d7da 100%);
  color: #151a40;
}

</style>
