import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/Home';
import SignIn from '@/views/SignIn';
import SignUp from '@/views/SignUp';
import Test from '@/components/Sender'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      component: Home
    },
    {
      path: '/signin',
      component: SignIn
    },
    {
      path: '/signup',
      component: SignUp
    },
    {
      path: '/test',
      component: Test
    },
    {
      path: '/registration',
      component: SignUp
    },
    {
      path: '/login',
      component: SignIn
    }
  ]
})

